<template>
  <full-height-layout>
    <div slot="layout_title">標簽</div>

    <tag-view-top slot="layout_inner_head"></tag-view-top>

    <tag-view-body :tags="tagList" v-if="tagList.length > 0" slot="layout_inner_body"></tag-view-body>

  </full-height-layout>
</template>

<script>
import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import TagViewTop from './Top/TagViewTop.vue'
import TagViewBody from './Body/TagViewBody.vue'

import conn from '@/service/conn'

export default {
  components: { TagViewTop, FullHeightLayout, TagViewBody },
  name: 'TagView',
  data() {
    return {
      tagList: []
    }
  },
  created() {
    window.addEventListener('beforeunload', e => this.beforLoad(e))
  },
  destroyed() {
    window.removeEventListener('beforeunload', e => this.beforLoad(e))
  },
  mounted() {
    this.getTag()
    /*
    conn.tag.getList_Tag().then(res => {
      this.tagList = res
    }).catch(err => {
          tool.showToast('网络连接失败。')
        })
    */
  },
  beforeDestroy() {
    alert('销毁组件')
  },
  methods: {
    async getTag() {
      const res = await conn.tag.getList_Tag()
      if (res) {
        this.tagList = res
      } else {
        tool.showToast('网络连接失败。')
      }
    },

    goPage(path) {

      this.$router.push(path)

      this.$store.state.iframeSrc = this.$route.meta.link
      this.$store.state.iframeTitle = this.$route.meta.title
    },

    beforLoad(e) {
      console.log('Router =', this.$route.path)
      console.log('刷新了，或要关闭。')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


